"use client";

import { createContext, useContext, useEffect, useState } from "react";
import {
  CognitoUserPool,
  CognitoUser,
  AuthenticationDetails,
} from "amazon-cognito-identity-js";
import { jwtDecode } from "jwt-decode";

import { useRouter } from "next/navigation";
import { Button } from "@/components/ui/button";
import { Card, CardContent } from "@/components/ui/card";

const poolData = {
  UserPoolId: "eu-west-2_Qv91VSnrS",
  ClientId: "533mf4s6jof94htjam2dut05hd",
};

const userPool = new CognitoUserPool(poolData);

export function signIn(username, password) {
  const authenticationDetails = new AuthenticationDetails({
    Username: username,
    Password: password,
  });

  const userData = {
    Username: username,
    Pool: userPool,
  };

  const cognitoUser = new CognitoUser(userData);

  return new Promise((resolve, reject) => {
    cognitoUser.authenticateUser(authenticationDetails, {
      onSuccess: (result) => {
        const idToken = result.getIdToken().getJwtToken();
        sessionStorage.setItem("idToken", idToken);
        sessionStorage.setItem("name", username);
        resolve(result);
      },
      onFailure: (err) => reject(err),
    });
  });
}

export function signOut() {
  sessionStorage.removeItem("idToken");
  sessionStorage.removeItem("name");
  const currentUser = userPool.getCurrentUser();
  if (currentUser) {
    currentUser.signOut();
  }
}

export function isSignedIn() {
  const idToken = sessionStorage.getItem("idToken");
  if (!idToken) {
    return false;
  }

  try {
    const decoded = jwtDecode(idToken);
    const currentTime = Math.floor(Date.now() / 1000);
    return decoded.exp > currentTime;
  } catch (error) {
    console.error("Invalid token", error);
    return false;
  }
}

export function getIdToken() {
  return sessionStorage.getItem("idToken");
}

export function getUsername() {
  return sessionStorage.getItem("name");
}

const AuthContext = createContext();

export const AuthProvider = ({ children }) => {
  const [isLoggedIn, setIsLoggedIn] = useState(false);
  const [authChecked, setAuthChecked] = useState(false);
  const router = useRouter();

  useEffect(() => {
    if (isSignedIn()) {
      setIsLoggedIn(true);
    }
    setAuthChecked(true);
  }, []);

  /*useEffect(() => {
    if (!isLoggedIn) {
      router.push('/login');
    }
  }, [authChecked, isLoggedIn, router]);*/

  async function login(username, password) {
    try {
      await signIn(username, password);
      setIsLoggedIn(true);
    } catch (error) {
      console.log(error);
    }
  }

  function logout() {
    signOut();
    setIsLoggedIn(false);
  }

  return (
    <AuthContext.Provider value={{ isLoggedIn, authChecked, login, logout }}>
      {children}
    </AuthContext.Provider>
  );
};

export const useAuth = () => {
  const context = useContext(AuthContext);
  if (!context) {
    throw new Error("useAuth must be used within an AuthProvider");
  }
  return context;
};

export function LogoutButton() {
  const { logout } = useAuth();
  const router = useRouter();

  const handleLogout = () => {
    logout(); // Update authentication state
  };

  return (
    <button
      onClick={handleLogout}
      class="bg-blue-500 hover:bg-blue-700 text-black font-bold py-2 px-4 rounded"
    >
      Logout
    </button>
  );
}

export function LoginForm() {
  const { login } = useAuth();
  const router = useRouter();

  const [darkMode, setDarkMode] = useState(false);

  useEffect(() => {
    if (darkMode) {
      document.documentElement.classList.add("dark");
    } else {
      document.documentElement.classList.remove("dark");
    }
  }, [darkMode]);

  function handleLogin(event) {
    event.preventDefault();
    const form = event.currentTarget;
    const username = form.elements.email.value;
    const password = form.elements.password.value;

    login(username, password);
    const intendedRoute = localStorage.getItem("intendedRoute") || "/";
    localStorage.removeItem("intendedRoute");
    router.push(intendedRoute); // Update authentication state
  }

  return (
    <div className="flex items-center justify-center min-h-screen bg-gray-100 dark:bg-gray-900">
      <Card className="w-full max-w-sm p-6 rounded-2xl shadow-lg bg-white dark:bg-gray-800">
        <div className="flex justify-between items-center mb-6">
          <h2 className="text-xl font-semibold text-gray-800 dark:text-white">Login</h2>
          <Button
            variant="ghost"
            className="p-2 rounded-full"
            onClick={() => setDarkMode(!darkMode)}
          >
            {darkMode ? '‚òÄÔ∏è' : 'üåô'}
          </Button>
        </div>
        <CardContent>
          <form className="space-y-4">
            <div>
              <label className="block text-gray-700 dark:text-gray-300">Email</label>
              <input
                type="email"
                placeholder="Enter your email"
                className="mt-1 w-full p-2 border rounded-md dark:bg-gray-700 dark:text-white"
              />
            </div>
            <div>
              <label className="block text-gray-700 dark:text-gray-300">Password</label>
              <input
                type="password"
                placeholder="Enter your password"
                className="mt-1 w-full p-2 border rounded-md dark:bg-gray-700 dark:text-white"
              />
            </div>
            <Button className="w-full bg-blue-500 hover:bg-blue-600 text-white py-2 px-4 rounded-lg">
              Sign In
            </Button>
          </form>
        </CardContent>
      </Card>
    </div>
  );
}

