"use client";
import "./globals.css";
import { useAuth, AuthProvider, LoginForm, LogoutButton } from "./auth";
import Link from "next/link";
import Image from "next/image";

import { useState, useEffect } from 'react';
//import { motion } from 'framer-motion';

function SideBar() {
  const menuItems = [
    { name: "Home", path: "/" },
    { name: "Boards", path: "/boards" },
    { name: "Tasks", path: "/tasks" },
  ];

  return (
    <div>
      <ul>
        {menuItems.map((item) => (
          <li key={item.path} className="mb-2">
            <Link
              href={item.path}
              className="block p-2 rounded text-blue-900 hover:bg-gray-700"
            >
              {item.name}
            </Link>
          </li>
        ))}
      </ul>
    </div>
  );
}

export default function RootLayout({ children }) {
  return (
    <html lang="en" className="bg-[#BBB2A9]">
      <AuthProvider>
        <BodyWithAuthCheck>{children}</BodyWithAuthCheck>
      </AuthProvider>
    </html>
  );
}

function Loading() {
  return (
    <body className="flex justify-center items-center h-screen">
      <p>Loading...</p>
    </body>
  );
}

function Login() {
  return (
    <body  className="bg-[#BBB2A9]">
      <main className="flex">
        <LoginForm />
      </main>
    </body>
  );
}

function Body({ children }) {
  const [darkMode, setDarkMode] = useState(false);

  useEffect(() => {
    const storedTheme = localStorage.getItem('darkMode');
    if (storedTheme) {
      setDarkMode(storedTheme === 'true');
    }
  }, []);

  useEffect(() => {
    localStorage.setItem('darkMode', darkMode);
  }, [darkMode]);


  return (
    <body>
      <div className={`flex h-screen transition-colors ${darkMode ? 'bg-gray-900 text-white' : 'bg-gray-100 text-gray-900'}`}>
      {/* Sidebar */}
      <aside className={`w-50 p-4 transition-colors fixed top-16 left-0 h-full ${darkMode ? 'bg-gray-800' : 'bg-white'} shadow-lg`}>
        <h2 className="text-xl font-semibold mb-4">Menu</h2>
        <ul className="space-y-2">
          <li><a href="#" className="block py-2 px-3 rounded-lg hover:bg-gray-200 dark:hover:bg-gray-700">Dashboard</a></li>
          <li><a href="#" className="block py-2 px-3 rounded-lg hover:bg-gray-200 dark:hover:bg-gray-700">Profile</a></li>
          <li><a href="#" className="block py-2 px-3 rounded-lg hover:bg-gray-200 dark:hover:bg-gray-700">Settings</a></li>
        </ul>
      </aside>
      
      <div className="flex-1 flex flex-col ml-64">
        {/* Header */}
        <header className={`fixed top-0 left-0 w-full flex justify-between items-center p-4 transition-colors ${darkMode ? 'bg-gray-800' : 'bg-white'} shadow-md z-10`}>
          <div className="flex items-center ml-64">
            <Image src="/mug.png" width="40" height="49" alt="Logo" />
            <h1 className="text-xl font-semibold">potteringabout</h1>
          </div>
          <nav className="space-x-4">
            <a href="#" className="hover:underline">Home</a>
            <a href="#" className="hover:underline">About</a>
            <a href="#" className="hover:underline">Contact</a>
          </nav>
          <button 
            onClick={() => setDarkMode(!darkMode)} 
            className="p-2 rounded-full bg-gray-200 dark:bg-gray-700 transition-colors"
          >
            {darkMode ? '‚òÄÔ∏è' : 'üåô'}
          </button>
        </header>
        
        {/* Main Content */}
        <main className="flex-1 p-6 overflow-auto mt-16">
          <h2 className="text-2xl font-semibold">Welcome</h2>
          <p className="mt-4">This is the main content area. You can put anything here.</p>
        </main>
      </div>
    </div>
    </body>
  );
}

function BodyWithAuthCheck({ children }) {
  const { authChecked, isLoggedIn } = useAuth();

  if (!authChecked) {
    return <Loading />;
  }

  return isLoggedIn ? <Body>{children}</Body> : <Login />;
}
